import { changePageAnimation } from '../../utils'

export function HelloPage1(props) {
    const { changePage } = props
    return (
        <div
            data-page='page'
            className='animate__animated animate__slideInRight hellow-page1  hellow-page1 hellow-page'
        >
            <p>   ,   !</p>
            <p>
                   ,   
                !
            </p>
            <button
                className='btn'
                data-page='1'
                onClick={(e) => {
                    setTimeout(() => {
                        changePage(e.target.dataset.page)
                    }, 350)
                    changePageAnimation()
                }}
            >
                
            </button>
        </div>
    )
}
